import React, { Component } from "react";
//eslint-disable-next-line
import { Link } from "react-router-dom";
import "../App.css";

class Features extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <React.Fragment>
        <div className="App">
          <header className="App-header">
            <div className="jumbotron">
              <h1 className="display-3">Hello peeps!</h1>
              <p className="lead">This is a Dashboard For A Bot - Highly WIP</p>
              <hr className="my-4" />
              <p>
                We Are Developing This Dasboard Right Now, Dont Forget to check
                it back later!
              </p>
              <p className="lead">
                <Link
                  className="btn btn-primary btn-lg"
                  target="_blank"
                  to="invite"
                  role="button"
                >
                  Invite
                </Link>
              </p>
            </div>
          </header>
        </div>
      </React.Fragment>
    );
  }
}

export default Features;
