import React, { Component } from 'react';
import '../static/index.css';
import '../App.css'

class Stats extends Component {
    constructor(props) {
        super(props)
        this.state = {
            members: 0
        }
    }

    getStats() {
        fetch('https://team-godl.ml/api/users/@me').then(res => res.json()).then(json => {
            this.setState({ members: json.users.toLocaleString() });
        })
    }

    componentDidMount() {
        this.getStats();
        this.ay = setInterval(() => {
            this.getStats();
        }, 2500)
    }
    lol() {
        return null
    }

    render() {
        return (

            <React.Fragment>
                <div className="App">
                    <header className="App-header">
                        <strong>Bot Statistics:</strong>
                        <br /> Users: {this.state.members}
                    </header></div>
            </React.Fragment>
        )
    }
}

export default Stats;