//eslint-disable-next-line
import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Dashboard from './components/Dashboard';
import Navbar from './components/Navbar';
import Auth from './components/Auth';
import Stats from './components/Stats';
import Features from './components/Features'
import ServerSelect from './components/ServerSelect';
import Footer from './components/Footer';
//eslint-disable-next-line

class Routes extends Component {
  render() {
    return (
      <BrowserRouter>
        <div>
          <Navbar />
          <Switch>
            <Route exact path="/" component={Features} />
            <Route exact path="/servers" component={ServerSelect} />
            <Route exact path="/features" component={Features} />
            <Route exact path="/stats" component={Stats} />
            <Route exact path="/dashboard" component={ServerSelect} />
            <Route exact path="/dashboard/:id" component={Dashboard} />
            <Route exact path="/auth" component={Auth} />
            <Route path="/invite" componet={() => { window.location = 'https://google.co.in'; return null; }} />
            <Route path="/github_owner" component={() => { window.location = 'https://github.com/ionadev'; return null; }}/>
          </Switch>
         <Footer />
        </div>
      </BrowserRouter>
    );
  }
}

export default Routes;