import React,{ Component } from 'react';
import {Link} from 'react-router-dom';
import StickyFooter from 'react-sticky-footer';
class Footer extends Component{
     constructor(props){
        super(props);
        this.props = props;
    } 

    render(){
      return(
        <StickyFooter
        align="center"
        bottomThreshold={30}
        normalStyles={{
        backgroundColor: "#3a3f44",
        position: "absolute",
        bottom: 0,
        padding: "2rem"
        }}
        stickyStyles={{
        position: "absolute",
        bottom: 0,
        backgroundColor: "#3a3f44",
        padding: "10px"
        }}
    >   <div align="center"><span className="fas fa-copyright" /> 2019 - Present <strong><Link to="/github_owner">Piyush Bhangale</Link></strong></div>
    </StickyFooter>
    
      )  
    }
}

export default Footer;