import React, { Component } from "react";
import "../static/index.css";
import logo from "./logo.png";
import { Link } from "react-router-dom";

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: null
    };
  }

  async getUser() {
    if (localStorage.getItem("token")) {
      let user = await fetch("https://react-backend.glitch.me/api/users/@me", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      });

      this.setState({ user: await user.json() });
    }
  }

  componentDidMount() {
    this.getUser();
  }

  login(){
     window.location="https://discordapp.com/oauth2/authorize?client_id=538601536851542016&scope=identify&redirect_uri=https%3A%2F%2Freact-backend.glitch.me%2Fapi%2Fauth%2Fcallback&response_type=code";
     return null;
  }

  async logout() {
    await localStorage.removeItem('token');
    document.location.href = "/"
  }
  render() {
    if (!this.state.user) {
      return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
          <Link className="navbar-brand" to="/">
            <img alt="Web dashboard test" src={logo} width="28" height="28" />
          </Link>
          <Link className="navbar-brand" to="/">
            Web Dashboard
          </Link>

          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarColor01"
          >
            <span className="navbar-toggler-icon" />
          </button>

          <div className="collapse navbar-collapse" id="navbarColor01">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item">
                <Link className="nav-link" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/features">
                  Features
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/dashboard">
                  Dashboard
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/invite">
                  Invite
                </Link>
              </li>
              <li>
                <Link className="nav-link" to="/stats">
                  Stats
                </Link>
              </li>
            </ul>
            <button type="button" className="btn btn-secondary" onClick={this.login}>
              Log In With <span className="fab fa-discord" />
              <br />
            </button>
          </div>
        </nav>
      );
    } else {
      return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
          <Link className="navbar-brand" to="/">
            <img alt="Web dashboard test" src={logo} width="28" height="28" />
          </Link>
          <Link className="navbar-brand" to="/">
            Web Dashboard
          </Link>

          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarColor01"
          >
            <span className="navbar-toggler-icon" />
          </button>

          <div className="collapse navbar-collapse" id="navbarColor01">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item">
                <Link className="nav-link" to="/">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/features">
                  Features
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/dashboard">
                  Dashboard
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/invite">
                  Invite
                </Link>
              </li>
              <li>
                <Link className="nav-link" to="/stats">
                  Stats
                </Link>
              </li>
            </ul>
            <button
              type="button"
              className="btn btn-secondary"
              onClick={this.logout}
            >
              <span className="fab fa-discord" /> {this.state.user.username}#{this.state.user.discriminator} (Logout)
              <br />
            </button>
          </div>
        </nav>
      );
    }
  }
}

export default Navbar;
