import React, { Component } from "react";
import { Link } from "react-router-dom";
import ToolTip from "react-tooltip";
import '../static/index.css'
class ServerSelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      guilds: []
    };
  }

  async getGuilds() {
    if (localStorage.getItem("token")) {
      let guilds = await fetch(
        "https://react-backend.glitch.me/api/guilds/@me",
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`
          }
        }
      );
      this.setState({ guilds: await guilds.json() });
    } else {
      window.location =
        "https://discordapp.com/oauth2/authorize?client_id=538601536851542016&scope=identify&redirect_uri=https%3A%2F%2Freact-backend.glitch.me%2Fapi%2Fauth%2Fcallback&response_type=code";
    }
  }

  componentDidMount() {
    this.getGuilds();
  }

  renderGuilds(guild) {
    return (
      <div
        key={guild.id}
        style={{ display: "inline-block", paddingLeft: 5, paddingRight: 5 }}
      >
        <Link to={`/dashboard/${guild.id}`}>
          <img
            className="image is-96x96"
            data-tip={guild.name}
            style={{ borderRadius: "50%" }}
            alt={guild.name}
            src={guild.iconUrl}
          />
        </Link>
        <ToolTip />
      </div>
    );
  }

  render() {
    const guilds = [];
    this.state.guilds.map(g => {
      return guilds.push(this.renderGuilds(g));
    });
    return (
        
        <React.Fragment>
        <div style={{ textAlign: "center" }}>
        <br/>
          <div className="containerab">
            <strong className="title is-1 has-text-white">
              Select a Server
            </strong>
            <div style={{ paddingTop: 75 }}>{guilds}</div>
            <br />
          </div>
        </div>
        </React.Fragment>
    );
  }
}

export default ServerSelect;
